.conteiner {
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: 767px) {
    max-width: 320px;
    padding-left: 15px;
    padding-right: 15px;
  }

  @media screen and (min-width: 768px) {
    max-width: 634px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 822px;
  }
}
